@-webkit-keyframes ripple{
  0%{
    -webkit-transform: scale(0.6);
    transform: scale(0.6);
  }

  50%{
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }

  100%{
    -webkit-transform: scale(0.6);
    transform: scale(0.6);
  }
}

@keyframes ripple{
  0%{
    -webkit-transform: scale(0.6);
    transform: scale(0.6);
  }

  50%{
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }

  100%{
    -webkit-transform: scale(0.6);
    transform: scale(0.6);
  }
}

