@keyframes ripple{
  0%{
    transform: scale(0.6);
  }
  
  50%{
    transform: scale(0.8);
  }
  
  100%{
    transform: scale(0.6);
  }
}

.circle{
  position: absolute;
  border-radius: 50%;
  background:#EF7924;
  animation: ripple 20s infinite;
  box-shadow: 0px 0px 1px 0px #508fb9;
  z-index: -1;
left:-10vw;
  top:-10vh;
  overflow-x: hidden!important;
}
.ripple-background{
  overflow: hidden;
}
